import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import Switch from "rc-switch";
import moment from "moment";
import ShopService from '../../../service/graphql/Shop'
import HubService from '../../../service/graphql/Hub'
import Pagination from "../../../components/pages/Pagination";
import timezonelist from "../../../data/timezonelist";


export default class ShopDayDetail extends Component {
  constructor(props) {
    super(props);

    this.state = {
      startDate: null,
      endDate:null,
      startDateTime: null,
      startDateRange: null,
      endDateRange: null,
      embeddedDate: moment(),
      highlightWithRanges :[],
      inline:true,
      availableWeekday:true,
      updatePageCnt:0,
      weekdays:[true,true,true,true,true,true,true],
      specialDays:[],
      selecteDate:null,
      start:null,
      end:null,
      openDate:null,
      selectsStart:false,
      selectsEnd:true,
      showDeleteModal:false,
      deleteIndex:null,
      showAddNewModal:false,
      refreshIndex:'',
      selectedHighlightWithRanges :[],
      totalPage:2,
      currentPage:1,
      timezone:'',
      today:null,
      timezoneList:timezonelist,
      timezoneName:'',
      timezone:{},
      timezoneOffset:''
    };
  }
  componentDidMount(){
    // this.getShopSpecialDays()
    this.getAvailableWeekdays()
    this.getHubTimings()    
  }
  calcTimezoneOffset(){
    var date = new Date(2016, 4, 10); 
    var timezoneDay=date.toLocaleString('en-US', { timeZone: this.state.timezoneName });
    timezoneDay = new Date(timezoneDay);
    const localTime=date.getTime()/1000
    const timezoneTime=timezoneDay.getTime()/1000
    const localOffset=date.getTimezoneOffset()*60
    const realTime=localTime+localOffset
    const timezoneOffset=realTime -timezoneTime
    this.setState({timezoneOffset}) 
    this.getShopSpecialDays()
  }
  getHubTimings(){
    const todayLocal= new Date()
    if(this.props.location.state && this.props.location.state.hubId){
      const params ={
        _id:this.props.location.state.hubId,
      }
      HubService.getHubTimings(params,(response)=>{
        if (response.status === 1) {
          const timezoneName=response.data.timzone?response.data.timzone:''
          this.setState({timezoneName})
          const timezone=this.state.timezoneList.find(item=>item.value===timezoneName)
          this.setState({timezone})
          var timezoneDay=todayLocal.toLocaleString('en-US', { timeZone: timezoneName });
          this.setState({today:moment(timezoneDay)})
          this.calcTimezoneOffset()
        } else {
        }
      })
    }
    else{
    }   
  }
  
  getShopSpecialDays(){
    if(this.props.match.params.id &&  this.props.match.params.id!=="0"){
      const params ={
        _id:this.props.match.params.id,
      }
      ShopService.getShopSpecialDays(params,(response)=>{
        if (response.status === 1) {
          const res = response.data;
          console.log('special days...',response.data)
          console.log('timezoneOffset...',this.state.timezoneOffset)
          if(res&&res.length>0){
            res.map((item)=>{
              item.disable_start=item.disable_start-this.state.timezoneOffset
              item.disable_end=item.disable_end-this.state.timezoneOffset
            })
            this.setState({specialDays:res})
            this.setHighlightWithRanges(res)
            if(res&&(Math.round(res.length/2))<2){
              this.setState({totalPage:2})
            }
            else{
              this.setState({totalPage:(Math.round(res.length/2))})
            }
          } 
        } else {
        }
      })
    }
    else{
    }   
  }   
  getAvailableWeekdays(){
    if(this.props.match.params.id &&  this.props.match.params.id!=="0"){
      const params ={
        _id:this.props.match.params.id,
      }
      ShopService.getAvailableWeekdays(params,(response)=>{
        if (response.status === 1) {
          const res = response.data;
          if(res.weekdays&&res.weekdays.length>0){
            this.setState({weekdays:res.weekdays})
          } 
        } else {
        }
      })
    }
    else{
    }   
  } 
  postAvailableWeekdays(){
    let {weekdays}=this.state
    if(this.props.match.params.id &&  this.props.match.params.id!=="0"){
      let weekdaysTmp = '['
      weekdaysTmp += `${weekdays}`
      weekdaysTmp += ']'
      const params ={
        _id:this.props.match.params.id,
        weekdays:weekdaysTmp
      }
      ShopService.postAvailableWeekdays(params,(response)=>{
        console.log("postAvailableWeekdays res...", response)
        if (response.status === 1) {
          this.props.history.push(`/app/shops/shop-list`)
          // weekdays = response.data.weekdays;
          // if(weekdays&&weekdays.length>0){
          //   this.setState({weekdays})
          // } 
        } else {
        }
      })
    }
    else{
    }    
  } 
  postShopSpecialDays(){
    let {specialDays,timezoneOffset}=this.state
    if(this.props.match.params.id &&  this.props.match.params.id!=="0"){
      let specialDaysTmp = '['
      for (let i = 0; i < specialDays.length; i++) {
        const disable_start = specialDays[i].disable_start + timezoneOffset;
        const disable_end = specialDays[i].disable_end + timezoneOffset;
        specialDaysTmp += `{disable_start: ${disable_start},disable_end: ${disable_end}},`
      }
      specialDaysTmp += ']'
      const params ={
        _id:this.props.match.params.id,
        specialDays:specialDaysTmp
      }
      console.log('postShopSpecialDays params...',params)

      ShopService.postShopSpecialDays(params,(response)=>{
      if (response.status === 1) {
        this.props.history.push(`/app/shops/shop-list`)
      } else {
      }
      })
    }
    else{
     
    }    
  } 
  onChangeProperty(idx){
    var {weekdays}= this.state
    weekdays[idx] = !weekdays[idx]
    this.setState({weekdays})    
  }   
  handleChangeDate = date => {
    console.log('date...',date)
    this.setState({selectedHighlightWithRanges:[]})
    if(!this.state.startDate||this.state.endDate){
      this.setState({ startDate:date });
      this.setState({ endDate:null });
      this.setState({ selectsStart:false });
      this.setState({ selectsEnd:true });
    }
    else if(date<this.state.startDate){
      this.setState({ startDate:date });
      this.setState({ endDate:null });
      this.setState({ selectsStart:false });
      this.setState({ selectsEnd:true });
    }
    else{
      this.setState({ endDate:date });
      this.setState({ selectsStart:false });
      this.setState({ selectsEnd:false });
    }
  };
  addShopSpecialDay() {
    var {startDate,endDate,specialDays}= this.state
    if(startDate&&endDate){
      startDate = new Date(startDate).getTime()/1000;
      endDate = new Date(endDate).getTime()/1000;
      const refreshSpecialDay={disable_start:startDate,disable_end:endDate}
      specialDays.splice(0, 0,refreshSpecialDay);
      this.setState({specialDays})
      if(specialDays&&(Math.round(specialDays.length/2))<2){
        this.setState({totalPage:2})
      }
      else{
        this.setState({totalPage:(Math.round(specialDays.length/2))})
      }
      console.log('e,totalPage...1',this.state.totalPage)
      this.setHighlightWithRanges(specialDays)
    }
    this.setState({showAddNewModal:false})
  }
  refreshShopSpecialDay(index,e) {
    const date =new Date()
    var {startDate,endDate,specialDays}= this.state
    if(startDate&&endDate){
      startDate = new Date(startDate).getTime()/1000;
      endDate = new Date(endDate).getTime()/1000;
      const disable_start = startDate;
      const disable_end = endDate;
      const refreshSpecialDay={disable_start:disable_start,disable_end:disable_end}
      // var timezoneDay=result.toLocaleString('en-US', { timeZone: this.state.timezoneName });
      specialDays.splice(index, 1,refreshSpecialDay);
      this.setState({specialDays})
      this.setHighlightWithRanges(specialDays)
    }
    this.setState({showAddNewModal:false})
  }
  deleteShopSpecialDay() {
    var {specialDays, deleteIndex}= this.state
    specialDays.splice(deleteIndex, 1);
    this.setState({specialDays})
    this.setHighlightWithRanges(specialDays)
    if(specialDays&&(Math.round(specialDays.length/2))<2){
      this.setState({totalPage:2})
    }
    else{
      this.setState({totalPage:(Math.round(specialDays.length/2))})
    }
    console.log('e,totalPage...2',this.state.totalPage)
    this.toggle()
  }

  setHighlightWithRanges(days){
    var highlightWithRangesTmp = []
    var highlightWithRangesTmpTest=[]
    var disableDays=''
    days.map((item,index)=>{
      disableDays=(item.disable_end-item.disable_start)/(60 * 60 * 24)
      for (let i = 0; i < disableDays+1; i++) {
        var result= (item.disable_start) * 1000
        result = new Date(result); 
        result.setDate(result.getDate() + i);
        // var timezoneDay=result.toLocaleString('en-US', { timeZone: this.state.timezoneName });
        // timezoneDay = new Date(timezoneDay);
          highlightWithRangesTmp.push(moment(result))
      }
    })
    this.setState({startDate:null,endDate:null})
    this.setState({highlightWithRanges:highlightWithRangesTmp})
  }
  subDays(date,days){
    var result = new Date(date);
    result.setDate(result.getDate() - days);
    return moment(result);
  }

  addDays(date, days) {
    var result = new Date(date);
    result.setDate(result.getDate() + days);
    return moment(result);
  }
  handleMonthChange = date => {
  }
  getAvailableWeekDay= date => {
    var result = new Date(date);
    var weekDay = result.getDay();
    return this.state.weekdays[weekDay];
  }
  getDate= date => {
    var result = new Date(date);
    var weekDay = result.getDay();
    var day = result.getDate();
    return weekDay;

  }
  onSubmit(e){
    this.postAvailableWeekdays()
    this.postShopSpecialDays()
    
  }
  onclose(e){
    this.props.history.push(`/app/shops/shop-list`)
  }
  showDeleteModal(index,e){
    console.log('e,index...',index)
    e.stopPropagation();
    e.preventDefault();
    this.setState({deleteIndex:index})
    this.setState({showDeleteModal:true})
  }
  onShowAddNewModal(index,e){
    let selectedHighlightWithRanges=[]
    if(index!==null){
      let selectedDays=this.state.specialDays[index]
      selectedHighlightWithRanges=[this.state.specialDays[index]]
      let openDate= (selectedDays.disable_start)* 1000
      openDate=new Date(openDate)

      this.setState({openDate:moment(openDate)})
      this.setEditHighlightRanges(selectedHighlightWithRanges)
    }
    else{
      console.log('test...')
      this.setState({selectsStart:null,selectsEnd:null,selected:null})
      this.setState({selectsStart:null,selectsEnd:null})
      this.setState({selectedHighlightWithRanges:[]})
      this.setEditHighlightRanges([])

    }
    this.setState({refreshIndex:index})
    this.setState({showAddNewModal:true})
  }
  setEditHighlightRanges(days){
    var highlightWithRangesTmp = []
    var disableDays=''
    // if (days&&days.length>0){
      days.map((item,index)=>{
        disableDays=(item.disable_end-item.disable_start)/(60 * 60 * 24)
        for (let i = 0; i < disableDays+1; i++) {
          var result= (item.disable_start) * 1000
          result = new Date(result); 
          result.setDate(result.getDate() + i);
          // var timezoneDay=result.toLocaleString('en-US', { timeZone: this.state.timezoneName });
          // timezoneDay = new Date(timezoneDay); 
          highlightWithRangesTmp.push(moment(result))
        }
      })
      this.setState({startDate:null,endDate:null})
      this.setState({selectedHighlightWithRanges:highlightWithRangesTmp})
    // }
  }
  toggle =()=>{
    this.setState({
      showDeleteModal: !this.state.showDeleteModal
    });
  }
  toggleAddNewModal =()=>{
    this.setState({
      showAddNewModal: !this.state.showAddNewModal,
      // startDate:null,
      // endDate:null
    });
  }
  onChangePage(i){
    console.log('pagenation...',i)
    this.setState({currentPage:i})
  }
  changeTimezon(date,timezoneName){
    const localDate=new Date(date)
    var timezoneDay=localDate.toLocaleString('en-US', { timeZone: timezoneName });
    timezoneDay=moment(timezoneDay).format("YYYY/MM/DD")
    return timezoneDay
  }
    render() {
      const {selectedHighlightWithRanges,selectsEnd,selectsStart,specialDays,openDate,today,
        startDate,endDate,selectedShopId,highlightWithRanges,inline,selectedItem,weekdays} = this.state
        return (
            <Fragment>
              <Row>
                <Colxx xxs="12">
                  <Breadcrumb heading={selectedShopId==='0'?"New Shop Days":"Shop Days Detail"} match={this.props.match} />
                  <Separator className="mb-5" />
                </Colxx>
              </Row>
              <Row className="mb-4">
                <Colxx xxs="12" md="12">
                  <Card>
                    <CardBody>
                      <CardTitle style={{marginBottom:'10px' }}>
                        Shop Days Detail
                      </CardTitle>
                      <Form>
                        {/* <Label>
                          Shop Days
                        </Label> */}
                        <div>
                          <DatePicker
                            selected={today}
                            onChange={this.handleChangeDate}
                            highlightDates={highlightWithRanges}
                            monthsShown={2}
                            inline
                            dayClassName={date =>
                              this.getAvailableWeekDay(date) ==  false ? "red-day-color" : undefined
                            }
                          />
                        </div>
                        <Label className="mt-4">
                          Available Weekdays
                        </Label>
                        <div>
                          <CustomInput
                            type="checkbox"
                            id="Su"
                            checked={weekdays[0]}
                            onChange={() => this.onChangeProperty(0)}
                            label="Su"
                            inline
                          />
                          <CustomInput
                            type="checkbox"
                            id="Mo"
                            checked={weekdays[1]}
                            onChange={() => this.onChangeProperty(1)}
                            label="Mo"
                            inline
                          />
                          <CustomInput
                            type="checkbox"
                            id="Tu"
                            checked={weekdays[2]}
                            onChange={() => this.onChangeProperty(2)}
                            label="Tu"
                            inline
                          />
                          <CustomInput
                            type="checkbox"
                            id="We"
                            checked={weekdays[3]}
                            onChange={() => this.onChangeProperty(3)}
                            label="We"
                            inline
                          />
                          <CustomInput
                            type="checkbox"
                            id="Th"
                            checked={weekdays[4]}
                            onChange={() => this.onChangeProperty(4)}
                            label="Th"
                            inline
                          />
                          <CustomInput
                            type="checkbox"
                            id="Fr"
                            checked={weekdays[5]}
                            onChange={() => this.onChangeProperty(5)}
                            label="Fr"
                            inline
                          />
                          <CustomInput
                            type="checkbox"
                            id="Sa"
                            checked={weekdays[6]}
                            onChange={() => this.onChangeProperty(6)}
                            label="Sa"
                            inline
                          />
                          
                        </div>
                        
                        <Row>
                          <Colxx xxs="6" md="6" style={{flex:1}}>
                            <Label className="mt-4">
                              Special Days
                            </Label>
                          </Colxx>
                          <Colxx xxs="6" md="6"className="display-flex flex-row-reverse top-right-button-container align-item-right">
                            <Button
                              color="primary"
                              size="sm"
                              className="top-right-button"
                              onClick={this.onShowAddNewModal.bind(this,null)}>
                              <IntlMessages id="pages.add-new" />
                            </Button>
                          </Colxx>
                        </Row>
                          <Card>
                            <CardBody>
                              <Table>
                                <thead>
                                  <tr>
                                    <th>Start Date</th>
                                    <th>End Date</th>
                                    <th>Action</th>
                                  </tr>
                                </thead>
                                <tbody>
                                  {specialDays.map((item, index) => {
                                    return (
                                      <tr
                                        key={"specialDays_" + index}
                                      >
                                        <td>
                                          {/* {this.changeTimezon((item.disable_start * 1000),this.state.timezoneName)} */}
                                          {moment((item.disable_start) * 1000).format("YYYY/MM/DD")}
                                        </td>
                                        <td>
                                          {/* {this.changeTimezon((item.disable_end * 1000),this.state.timezoneName)} */}
                                          {moment((item.disable_end) * 1000).format("YYYY/MM/DD")}
                                        </td>
                                        <td>
                                          <div className="action-group">
                                            <i style = {{cursor:'pointer'}} className="iconsminds-refresh" 
                                              onClick={this.onShowAddNewModal.bind(this,index)}
                                              />
                                            <i
                                              style = {{marginLeft:'5px'}} className="simple-icon-trash" 
                                              onClick={this.showDeleteModal.bind(this, index)}
                                            />
                                          </div>
                                        </td>
                                      </tr>
                                    );
                                  })}
                                </tbody>
                              </Table>
                              <Pagination
                                currentPage={this.state.currentPage}
                                totalPage={this.state.totalPage}
                                onChangePage={i => this.onChangePage(i)}
                              />
                            </CardBody>
                          </Card>
                          <Button color="secondary" className="mt-4" outline
                            onClick={()=>{
                              this.onclose()}}>
                              <IntlMessages id="forms.cancel" />
                            </Button>
                            <Button style={{marginLeft:'15px'}} color="primary" className="mt-4" onClick={()=>{
                              this.onSubmit()
                            }}>
                          <IntlMessages id="forms.submit" />
                        </Button>
                      </Form>
                    </CardBody>
                  </Card>
                </Colxx>
              </Row>
              <Modal isOpen={this.state.showAddNewModal} toggle={this.toggleAddNewModal}wrapClassName="modal-right"
                backdrop="static">
                <ModalHeader toggle={this.toggleAddNewModal}>
                {this.state.refreshIndex==null?"Add New Special Day": "Edit Special Day"}
                </ModalHeader>
                <ModalBody  className="align-item-center">
                <Label>
                  
                </Label>
                <DatePicker
                  selected={endDate}
                  onChange={this.handleChangeDate}
                  startDate={startDate}
                  endDate={endDate}
                  selectsStart={selectsStart}
                  selectsEnd={selectsEnd}
                  selectsRange
                  highlightDates={selectedHighlightWithRanges}
                  openToDate={openDate}
                  inline
                  dayClassName={date =>
                    this.getAvailableWeekDay(date) ==  false ? "red-day-color" : undefined
                  }
                />
                </ModalBody>
                <ModalFooter>
                <Button color="secondary" outline onClick={this.toggleAddNewModal}>
                    Cancel
                  </Button>
                  <Button color="primary" 
                    onClick={this.state.refreshIndex==null?this.addShopSpecialDay.bind(this):this.refreshShopSpecialDay.bind(this,this.state.refreshIndex)}>
                    Apply
                  </Button>{" "}
                </ModalFooter>
              </Modal>

              <Modal isOpen={this.state.showDeleteModal} toggle={this.toggle}>
                <ModalHeader toggle={this.toggle}>
                  Warning
                  {/* <IntlMessages id="modal.modal-title" /> */}
                </ModalHeader>
                <ModalBody>
                  Are you sure to delete this date?
                </ModalBody>
                <ModalFooter>
                  <Button color="primary" 
                  onClick={this.deleteShopSpecialDay.bind(this)}>
                    Yes
                  </Button>{" "}
                  <Button color="secondary" outline onClick={this.toggle}>
                    Cancel
                  </Button>
                </ModalFooter>
              </Modal>
          </Fragment>
        )
    }
}
